
const swiper = new Swiper('.js-article-swiper', {
    hashNavigation: true,
    slidesPerView: 1.2,
    spaceBetween: 24,


    pagination: {
        el: '.swiper-pagination',
        type: 'bullets',
    },

    history: {
        replaceState: true,
    },

    breakpoints: {
        768: {
          slidesPerView: 2.2,
        },
        992: {
          slidesPerView: 3.2,
        },
        1240: {
          slidesPerView: 4.2,
        }
      }

});