$(function () {
    const $accordionPersonal = $('.js-personal-accordion');
     
    if(!$accordionPersonal.length) return;

    $accordionPersonal.on("click", function () {
        const that = $(this);
        const nextEl = that.next();
        const nextHeight = nextEl[0].scrollHeight
        that.toggleClass("personal__head_active")

        if (that.hasClass("personal__head_active")) {
            return nextEl.css({
                "max-height": nextHeight,
                overflow: "visible",
                opacity: 1,
            })
        }

        nextEl.css({
            "max-height": 0,
            overflow: "hidden",
            opacity: 0,
        })
    })

})